html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,*:before, *:after {
  box-sizing: inherit;
}
body {
  font-size: 1.8rem;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
}

a:hover,a:focus {
  text-decoration: none;
}
.clear {
  clear:both;
}

.noselect {
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &::selection {
    background: transparent;
  }
}

.blur {
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
}